'use strict';
var React = require('react');
var runtimeHelper = require('../../helper/runtimeHelper');
var util = require('../../helper/util');

var SelectComp = React.createClass({
  propTypes: {
    checkedIndex: React.PropTypes.number.isRequired,
    closeSelectList: React.PropTypes.func.isRequired,
    selectListItem: React.PropTypes.func.isRequired,
    textStyle: React.PropTypes.object.isRequired,
    boxStyle: React.PropTypes.object.isRequired,
    dataSet: React.PropTypes.object.isRequired,
  },

  getInitialState: function () {
    return {
      onItemIndex: this.props.checkedIndex
    };
  },

  componentDidUpdate: function () {
    this.loadEvent();
  },

  domIndexArray: null,
  componentDidMount: function () {
    this.loadEvent();
    //this.domIndexArray = util.pushToTop($('.component-SelectComp'));
  },

  loadEvent: function () {
    var context = this;
    if (!document.onmouseup) {
      document.onmouseup = function (event) {
        //util.resetDomIndex(context.domIndexArray);
        context.props.closeSelectList({isExpand: false});
        //context.domIndexArray = null;
        document.onmouseup = null;
      };
    }
  },

  componentWillUnmount: function () {
    document.onmouseup = null;
  },

  render: function () {
    var self = this;

    var textStyle = this.props.textStyle;
    return (
      <div className='component-SelectComp'>
        <div style={this.props.boxStyle} className='select-item-box'>
          {this.props.dataSet.map(function (data, index) {
            var itemStyle = {
              width: this.props.width - 2,
              height: this.props.height,
              position: 'relative'
            }
            if (index === self.state.onItemIndex) {
              //itemStyle.background = '#1E90FF';
              itemStyle.background = '#41C7D6';
            }
            console.log(data.linkStyle);
            return (
              <div key={index}
                   style={itemStyle}
                   className={data.linkStyle}
              >
                <div
                  id={index}
                  className='select-item-cover'
                  //onMouseOver={this.onItem}
                  onClick={this.checkItem}/>
                <label style={textStyle} className='select-item-text'>{data.text}</label>
              </div>);

          }, this)}
        </div>
      </div>
    )

  },

  checkItem: function (e) {
    e.stopPropagation();
    e.preventDefault();
    e.nativeEvent.stopPropagation();
    var index = parseInt($(e.target).attr('id'), 10) || 0;
    this.props.selectListItem(e, index);
  },
  onItem: function (e) {
    var index = parseInt($(e.target).attr('id'), 10);
    this.setState({onItemIndex: index});
  },
});

module.exports = SelectComp;


